import cultureManager from "@/CultureManager";

export interface IColorInfo {
    id: number,
    description: string,
    weightDescription: string,
    imageSources: string[]
}

export class ColorInfo implements IColorInfo{
    id: number;
    description: string;
    weightDescription: string;
    imageSources: string[];
    constructor(id: number, description: string, weightDescription: string, imageSources: string[]) {
        this.id = id;
        this.description = description;
        this.weightDescription = weightDescription;
        this.imageSources = imageSources;
    }
}

