    private getMaterialDescriptionForId(id: number): string {
        const desc = ColorInfoProvider.materialDescriptions.find(d => d.id === id);
        return cultureManager.isEstonian ? desc!.descriptionEst : desc!.descriptionEng;
    }
    
    private getWeightDescriptionForId(id: number): string {
        const desc = ColorInfoProvider.weightDescriptions.find(d => d.id === id);
        return cultureManager.isEstonian ? desc!.descriptionEst : desc!.descriptionEng;
    }
    
